配套”角色。拉图尔也觉得自己长大的时候白人男性在超级英雄漫画中占主导地位，并且看到Gwen Stacy是一个潜在的女主角，以更好的方式代表女性，“事实上，这是一个女人确实改变了所有一切事物的意义和潜台词作为一个创造者，这真的很有趣，它开辟了一个前所未有的方向。2014年10月，Nick Lowe在纽约动漫展上宣布角色会经过大量需求后，她将获得自己正在进行的系列节目。第一期Spider-Gwen获得了商业上的成功，并且是2015年2月销量超过250,000份的第三畅销漫画。第一卷在第五期后结束，作为秘密战争事件的一部分，角色继续进入第二卷蜘蛛诗。事件结束后，同一创意团队的第二卷开始于第一期作为漫威全新，全能的一部分这是一部名为放射性蜘蛛-Gwen的漫威印记。蜘蛛侠是一部名为“蜘蛛女”的多部分交叉中的主角。它以Spider-Women Alpha＃1开始，以Spider-Women Omega＃1结束，Spider-Woman，Silk和Spider-Gwen的某些问题部分描绘了两者之间的故事情节。")]),_c('br'),_c('h2',[_vm._v("影视作品及游戏")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('v-tabs',{staticStyle:{"margin-top":"20px"},attrs:{"centered":"","color":"blue-grey lighten-5","icons-and-text":""}},[_c('v-tabs-slider',{attrs:{"color":"indigo darken-1"}}),_c('v-tab',{staticStyle:{"width":"30%"},attrs:{"href":"#tab-1"}},[_vm._v("\n      电影\n      "),_c('v-icon',[_vm._v("local_movies")])],1),_c('v-tab',{staticStyle:{"width":"30%"},attrs:{"href":"#tab-2"}},[_vm._v("\n      动画\n      "),_c('v-icon',[_vm._v("live_tv")])],1),_c('v-tab-item',{attrs:{"value":"tab-1"}},[_c('v-card',{attrs:{"flat":""}},[_c('v-card-text',[_c('blockquote',{staticClass:"blockquote"},[_c('br'),_vm._v(" \n    \n  Spider-Man: Into the Spider-Verse\n           ")])])],1)],1),_c('v-tab-item',{attrs:{"value":"tab-2"}},[_c('v-card',{attrs:{"flat":""}},[_c('v-card-text',[_c('blockquote',{staticClass:"blockquote"},[_vm._v("\nUltimate Spider-Man vs. The Sinister Six"),_c('br'),_vm._v("\n 2017 Spider-Man"),_c('br'),_vm._v("\n Marvel Rising: Initiation\n")])])],1)],1)],1)],1)}
